
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
name:'edit',
props:['id'],
data:function(){
return{
topic:{
title:'',
content:''
},
toolbars:{
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
strikethrough: true, // 
mark: true, // 
superscript: true, // 
subscript: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
readmodel: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
undo: true, // 
redo: true, // 
trash: true, // 
save: false, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true 
}
}
},
methods:{

edittopic:function(){
var app = this;
this.$http.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id,
title:this.topic.title,
content:this.topic.content,
tab:'dev'
}).then(function(res){
console.log(res);
app.$router.back();
})
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(res){
console.log(res.data);
vm.topic=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var app = this;
this.$http.get('/topic/'+to.params.id).then(function(res){
console.log(res.data);
vm.topic=res.data.data
})
next();
}
}



